/* General styles for the Archive component */
.archive-container {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #fff;
  overflow: hidden;
  border: 20px solid rgb(255, 255, 255);
}

.projects-list {
  flex: 1;
  max-width: 900px;
  background: white;
  backdrop-filter: blur(10px);
  color: black;
  padding: 20px;
  box-sizing: border-box;
  overflow-y: auto;
}

/* Adjust project item styles */
.project-item {
  margin-bottom: 15px;
  padding: 10px;
  cursor: pointer;
  border: 1px solid black;
  border-radius: 8px;
}

.project-item.active {
  background-color: black;
  color: white;
}

.project-preview {
  flex: 3;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  padding: 10px;
  border-radius: 20px;
  max-width: 800px;
  max-height: 800px;
  width: 100%;
  height: auto;
}

.contact-button {
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  padding: 8px 16px;
  border: none;
  cursor: pointer;
  font-size: 14px;
  border-radius: 8px;
  position: absolute;
  bottom: 20px;
}

.secure-icon {
  margin-right: 10px;
}

.project-name {
  font-size: 1.5em;
  font-weight: bold;
}

.project-description {
  font-size: 0.9em;
  color: #555;
}

/* Adjust styles for tablets and smaller screens */
@media (max-width: 1024px) {
  .project-preview {
    max-width: 100%;
    max-height: 400px; /* Adjust height for smaller screens */
    padding: 5px; /* Reduce padding */
  }
  
  .projects-list {
    padding: 15px; /* Adjust padding */
  }

  .contact-button {
    padding: 6px 12px; /* Smaller button */
    font-size: 12px; /* Smaller text */
  }

  .project-name {
    font-size: 1.3em; /* Slightly smaller name */
  }

  .project-description {
    font-size: 0.8em; /* Slightly smaller description */
  }
}

/* Hide desktop archive on screens smaller than 768px */
@media (max-width: 768px) {
  .archive-container {
    display: none;
  }
}
