.archive-mobile-container {
  width: 100vw;
  height: auto;

  display: flex;
  flex-direction: column;
  position: relative; /* Ensure it's not fixed or absolute */
  background-color: #f8f8f8;
  -webkit-overflow-scrolling: touch; /* Smooth scrolling on iOS */
  overflow-y: scroll; /* Enable scrolling only for this container */
  -webkit-overflow-scrolling: touch; /* Smooth scrolling on iOS */
}

.project-item-mobile {
  width: 90vw;
  height: auto;
  margin: 0 20px;
  margin-bottom: 30px;
  margin-top:  30px ;
  padding: 25px;
  border: 1px solid #ddd;
  border-radius: 20px;
  background-color: #fff;
  transition: transform 0.3s ease, border-color 0.3s ease;
  scroll-snap-align: start; /* Ensure snapping aligns with the start of the section */
}

.project-item-mobile.active {
  border-color: #000;
  transform: scale(1.05);
}

.project-name-mobile {
  font-size: 18px;
  font-weight: bold;
}

.project-description-mobile {
  font-size: 14px;
  color: #666;
}

.project-image-mobile {
  margin-top: 10px;
  height: 200px;
  background-size: cover;
  background-position: center;
  border-radius: 8px;
}

.archive-mobile-container::-webkit-scrollbar {
  display: none;
}

/* CSS Transition for project items */
.project-enter {
  opacity: 0;
  transform: translateY(20px);
}

.project-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 300ms, transform 300ms;
}

.project-exit {
  opacity: 1;
  transform: translateY(0);
}

.project-exit-active {
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 300ms, transform 300ms;
}

.contact-button-mobile {
  margin-top: 10px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #000;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.contact-button-mobile:hover {
  background-color: #444;
}

.secure-icon {
  margin-right: 5px;
}
